<template>
  <div class="col-5" style=" float:right; ">
    <div class="panel">

      <div class="panel-heading">
        <div class="panel-title">案件列表</div>
      </div>

      <div class="panel-body" style=" min-height:1075px;">

        <div class="cate">
          <ul>
            <li :class="{'active': showPage==1}" @click="showPage=1,init(1)">正在办理</li>
            <li :class="{'active': showPage==2}" @click="showPage=2,init(1)">已办结</li>
            <li :class="{'active': showPage==3}" @click="showPage=3,init(1)">我的收藏</li>
          </ul>
        </div>

        <div class="casesearch" v-show="showPage==1||showPage==2">
          <ul>
            <m1 @setParams="setIndustry" />
            <m2 @setParams="setSource" />
            <m3 @setParams="setIllegalActivity" />
            <m4 @setParams="setUnit" />
            <li>
              <el-input v-model="searchObj.keyword" placeholder="请输入关键词" @keydown.enter="init(1)"></el-input>
            </li>
            <li>
              <el-button type="primary" icon="el-icon-search" @click="init(1)">查询</el-button>
            </li>
          </ul>
        </div>

        <el-row>
          <el-table :data='item.content' stripe ref='table' style='width: 100%' class="table table-striped"
            v-loading="loading">
            <el-table-column min-width="200" show-overflow-tooltip label='案件名称' fixed>
              <template slot-scope="scope">
                <a href="javascript:void(0)"
                  @click="$router.push(`lawcaseinfo/${scope.row.id}`)">{{scope.row.caseFacts}}</a>
              </template>
            </el-table-column>
            <el-table-column prop='caseFacts' width="250" show-overflow-tooltip label='案件描述'>
            </el-table-column>
            <!-- <el-table-column width="100" show-overflow-tooltip label='办理状态'>
              <template slot-scope="scope">
                <span class="badge badge-primary">{{scope.row.caseStat}}</span>
              </template>
            </el-table-column> -->
            <el-table-column width="60" show-overflow-tooltip label='操作' fixed="right">
              <template slot-scope="scope">
                <div class="oprate">
                  <a href="javascript:void(0)" v-show="!scope.row.favorited" @click="favor(scope.row.id, 1)"><i
                      class="icon-favorite" title="收藏"></i></a>
                  <a href="javascript:void(0)" v-show="scope.row.favorited" @click="favor(scope.row.id, 0)"
                    class="active"><i class="icon-favorite" title="去掉收藏"></i></a>
                </div>
              </template>
            </el-table-column>
          </el-table>
        </el-row>
        <div class="pagebar" v-show="item.totalRecord>searchObj.pageSize">
          <div class="pageinfo">共有{{item.totalRecord}}条记录，每页显示{{searchObj.pageSize}}条</div>
          <el-pagination background layout="prev, pager, next" :total="item.totalRecord" :page-size="searchObj.pageSize"
            @current-change="init" :current-page.sync="searchObj.page">
          </el-pagination>
        </div>

      </div>

    </div>
  </div>
</template>

<script>
// import M1 from '@/views/lawcase/components/item/M1'
// import M2 from '@/views/lawcase/components/item/M2'
// import M3 from '@/views/lawcase/components/item/M3'
// import M4 from '@/views/lawcase/components/item/M4'
export default {
  data () {
    return {
      loading: false,
      showPage: 1,
      item: {
        content: []
      },
      searchObj: {
        caseSource: -1,
        industry: -1,
        illegalActivity: -1,
        handleUnit: -1,
        keyword: '',
        page: 1,
        pageSize: 10,
        caseStatus: '办理中'
      }
    }
  },
  components: {
    M1: () => import('./item/M1'),
    M2: () => import('./item/M2'),
    M3: () => import('./item/M3'),
    M4: () => import('./item/M4')
  },
  mounted () {
    this.init(1)
  },
  methods: {
    init (p) {
      var self = this
      this.item = {
        content: []
      }
      this.searchObj.page = p
      let api = 'A_S_CASE_ITEM'
      let params = this.searchObj
      if (this.showPage === 1) {
        params.caseStatus = '2'
      }
      if (this.showPage === 2) {
        params.caseStatus = '1'
      }
      if (this.showPage === 3) {
        api = 'A_S_CASE_FAVOR_ITEM'
        params = {
          page: p,
          pageSize: this.searchObj.pageSize
        }
      }
      this.loading = true
      this.$http.get({
        api: api,
        params: params,
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.item = res
        }
      })
    },
    setIndustry (i) {
      this.searchObj.industry = i
    },
    setSource (i) {
      this.searchObj.caseSource = i
    },
    setIllegalActivity (i) {
      this.searchObj.illegalActivity = i
    },
    setUnit (i) {
      this.searchObj.handleUnit = i
    },
    favor (id, status) {
      let self = this
      this.$http.get({
        api: 'A_S_CASE_FAVOR',
        params: {
          id: id,
          eventId: status
        },
        callback: (res) => {
          self.init(1)
        }
      })
    }
  }
}

</script>

<style scoped>
  .el-table--fit {
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
  }

</style>



